.select{position:relative;border:1px solid #dedede;padding:5px 20px 5px 10px;border-radius:5px;cursor:pointer;font-size:16px;line-height:20px}.select:after{content:"⌄";font-size:16px;font-weight:600;position:absolute;top:0px;right:10px;color:#333}.select.expanded{border-bottom-left-radius:0;border-bottom-right-radius:0}.select.expanded:after{content:"⌃";top:8px}.select-title{color:#333;overflow:hidden}.select.expanded,.select:hover{outline:1px solid #62b839;border:1px solid #62b839}.select-options{display:none;position:absolute;border-bottom-left-radius:5px;border-bottom-right-radius:5px;border:2px solid #62b839;background-color:#fff;left:-2px;top:30px;width:100%;z-index:1000}.expanded .select-options{display:block}.select-option{cursor:pointer;padding:5px 10px}.select-option:last-child{border-bottom-left-radius:5px;border-bottom-right-radius:5px}.select-option:hover{background-color:#62b839;color:#fff}