.customer-form{z-index:1005;width:100%;height:100%;position:absolute;top:0;left:0;background-color:rgba(0,0,0,.3)}.customer-form-wrap{display:flex;height:100%;overflow:hidden}.customer-form-wrap.left{width:100%}.customer-form-wrap.right{min-width:800px;margin-right:-860px;background-color:#fff;margin-left:auto;height:100%;display:flex;flex-direction:column;transition:margin-right .2s ease-in-out;padding:20px 30px}.customer-form-wrap.right.show{margin-right:0}.customer-form .form-title{font-size:24px;position:relative}.customer-form .form-title-close{background:#f5f5f5;border-radius:50%;position:absolute;top:0;right:0;width:32px;height:32px;cursor:pointer;transition:background-color .1s ease-in-out}.customer-form .form-title-close:hover{background:#333}.customer-form .form-title-close:hover:before{color:#fff}.customer-form .form-title-close:before{content:"✕";color:#333;font-size:18px;position:absolute;font-weight:bold;top:4px;left:8px;transition:color .1s ease-in-out}.customer-form .form-content{display:flex;padding:40px 0 0 0}.customer-form .form-content-left{width:50%;padding:0 20px 0 0}.customer-form .form-content-right{width:50%;padding:0 0 0 20px}.customer-form .form-content-item{padding:0 0 20px}.customer-form .form-content-item-label{padding:0 0 5px;color:#333}.customer-form .form-actions{display:flex;justify-content:flex-end}.customer-form .form-actions .btn{margin-left:20px}