.grid{display:block;width:100%}.grid-item{display:flex;align-items:flex-start;font-style:normal;font-weight:normal;font-size:13px;line-height:16px;letter-spacing:-0.01em;color:#202020;cursor:pointer;border-bottom:2px solid #f5f5f5;padding:16px 0 18px 0}.grid-item-active,.grid-item:hover{background:#f9f6ed;border-bottom:2px solid #f9f6ed}.grid-item-checkbox{padding:0 22px}.grid-item-checkbox input[type=checkbox]{-webkit-appearance:none;appearance:none;background-color:#fafafa;border:1px solid #dedede;padding:0px;height:15px;width:15px;display:inline-block;position:relative;margin:0}.grid-item-checkbox input[type=checkbox]:checked{background-color:#8a826a;border:1px solid #8a826a;color:#fff}.grid-item-checkbox input[type=checkbox]:checked:after{content:"✔";font-size:11px;position:absolute;top:-2px;left:2px;color:#fff}.grid-item-name,.grid-item-class,.grid-item-approver,.grid-item-team{padding:0 0 0 0}.grid-item-name{width:52%}.grid-item-class{width:10%}.grid-item-approver{width:18%}.grid-item-team{width:10%}.grid-item-header{padding:0}.grid-item-header .grid-item-checkbox{padding-top:16px;padding-bottom:14px;line-height:16px}.grid-item-header .grid-item-checkbox input[type=checkbox]{border:1px solid #000}.grid-item-header .grid-item-name,.grid-item-header .grid-item-class,.grid-item-header .grid-item-approver,.grid-item-header .grid-item-team{padding:19px 0 16px 0;font-weight:bold;font-size:9px;line-height:11px !important;text-transform:uppercase;color:#202020}